.social {
  max-width: 386px;

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }

  &__item {
    list-style-type: none;
  }

  &__link {
    color: $main-color;
    text-transform: uppercase;
    text-decoration: none;
  }
}